
Template.body.addContent((function() {
  var view = this;
  return HTML.DIV({
    "class": "container-fluid"
  }, "\n\n  ", Spacebars.include(view.lookupTemplate("accueil")), "\n\n  ", Spacebars.include(view.lookupTemplate("loginButtons")), "\n  \n  ", Blaze.Unless(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("currentUser"), "emails", "0", "verified"));
  }, function() {
    return "\n  ";
  }), " \n\n\n  ", Blaze.Unless(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("currentUser"), "emails", "0", "verified"));
  }, function() {
    return [ "\n  ", Spacebars.include(view.lookupTemplate("index")), "\n  " ];
  }, function() {
    return [ " \n", HTML.P("----------------------------------"), "\n  ", Spacebars.include(view.lookupTemplate("rechercheAnnonce")), "\n", HTML.P("----------------------------------"), "\n\n", HTML.DIV({
      "class": "row"
    }, "\n", HTML.DIV({
      "class": "col-md-4"
    }, "\n", HTML.P("----------------------------------"), "\n  ", Spacebars.include(view.lookupTemplate("affichage_annonce")), "\n  "), "\n  "), "\n  \n  ", HTML.P("----------------------------------"), "\n  ", HTML.DIV({
      "class": "row"
    }, "\n", HTML.DIV({
      "class": "col-md-3"
    }, "\n  ", HTML.H2("Insert:"), "\n  ", HTML.Comment("{{> creer_annonce}}"), "\n\n  ", Spacebars.include(view.lookupTemplate("insertAnnonceForm")), "\n  "), "\n  "), "\n\n", HTML.P("----------------------------------"), "\n\n\n", HTML.Comment("  <h2>Update:</h2>\n  {{> updateAnnonceForm}}\n"), "\n", HTML.P("**********************************"), "\n  ", Spacebars.include(view.lookupTemplate("ajout")), "\n  ", HTML.BR(), HTML.BR(), HTML.BR(), "\n\n  ", Spacebars.include(view.lookupTemplate("fieldsafterchoice")), "\n  " ];
  }), "\n   ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("result"));
  }, function() {
    return [ "\n      ", Spacebars.include(view.lookupTemplate("resultat")), "\n    " ];
  }), "\n  ");
}));
Meteor.startup(Template.body.renderToDocument);

Template.__checkName("accueil");
Template["accueil"] = new Template("Template.accueil", (function() {
  var view = this;
  return HTML.Raw("<h1>Bienvenue sur UniBooks</h1>");
}));

Template.__checkName("signup");
Template["signup"] = new Template("Template.signup", (function() {
  var view = this;
  return HTML.Raw('<div class="row">\n    <div class="col-xs-12 col-sm-6 col-md-4">\n      <h4 class="page-header">Cr√©ation de compte</h4>\n      <form id="signup" class="signup">\n        <div class="form-group">\n          <label for="emailAddress">Adresse Email</label>\n          <input type="email" name="emailAddress" class="form-control" value="@unil.ch">\n        </div>\n        <div class="form-group">\n          <label for="password">Mot de passe</label>\n          <input type="password" name="password" class="form-control" placeholder="Mot de passe">\n        </div>\n        <div class="form-group">\n          <input type="submit" class="btn btn-success" value="Cr√©er le compte">\n        </div>\n      </form>\n    </div>\n  </div>');
}));

Template.__checkName("index");
Template["index"] = new Template("Template.index", (function() {
  var view = this;
  return HTML.Raw('<p class="alert alert-warning">Vous devez proc√©der √† la v√©rification de compte via le lien envoy√© automatiquement √† l\'adresse email que vous avez sp√©cifi√©e avant de pouvoir utiliser UniBooks. <a href="#" class="resend-verification-link">Renvoyer le lien de v√©rification</a>.</p>');
}));

Template.__checkName("ajout");
Template["ajout"] = new Template("Template.ajout", (function() {
  var view = this;
  return HTML.Raw('<p>Rechercher un livre:</p>\n  <form name="searchitem">\n    Entrer l\'ISBN, le titre du livre ou l\'auteur: <input type="text" name="champinfo">\n    <input type="button" class="SearchIPA" name="infolivre" value="Search in Google IPA">\n  </form>');
}));

Template.__checkName("resultat");
Template["resultat"] = new Template("Template.resultat", (function() {
  var view = this;
  return HTML.LI({
    id: "koala",
    "class": function() {
      return [ "book ", Spacebars.mustache(view.lookup("selectedClass")) ];
    }
  }, Blaze.View("lookup:volumeInfo.title", function() {
    return Spacebars.mustache(Spacebars.dot(view.lookup("volumeInfo"), "title"));
  }), HTML.Raw("<br>"), Blaze.View("lookup:volumeInfo.authors", function() {
    return Spacebars.mustache(Spacebars.dot(view.lookup("volumeInfo"), "authors"));
  }), HTML.Raw("<br>"), HTML.IMG({
    src: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("volumeInfo"), "imageLinks", "thumbnail"));
    },
    alt: "Image non disponible"
  }), "\n    ");
}));

Template.__checkName("insertAnnonceForm");
Template["insertAnnonceForm"] = new Template("Template.insertAnnonceForm", (function() {
  var view = this;
  return Blaze._TemplateWith(function() {
    return {
      collection: Spacebars.call("AnnonceList"),
      id: Spacebars.call("insertAnnonceForm"),
      type: Spacebars.call("insert")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("quickForm"));
  });
}));

Template.__checkName("rechercheAnnonce");
Template["rechercheAnnonce"] = new Template("Template.rechercheAnnonce", (function() {
  var view = this;
  return HTML.Raw('<p>Rechercher un livre:</p>\n<form>\n<input type="text" placeholder="Entrez le nom de l\'auteur ou du livre"><br>\n<input type="button" value="Rechercher"><br>\n</form>');
}));

Template.__checkName("affichage_annonce");
Template["affichage_annonce"] = new Template("Template.affichage_annonce", (function() {
  var view = this;
  return HTML.Raw('<h2>Quelques annonces du moment...</h2>\n  <ul class="list-group">\n    <li class="list-group-item">\n      <h3>Annonce 1</h3>\n      <div>Prix</div>\n    </li>\n    <li class="list-group-item">\n      <h3>Annonce 2</h3>\n      <div>Prix</div> \n    </li>\n    <li class="list-group-item">\n      <h3>Annonce 3</h3>\n      <div>Prix</div> \n    </li>\n  </ul>');
}));

Template.__checkName("fieldsafterchoice");
